.xdata {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 60px;
}

.xdata-type {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.xdata-type-head {
    height: 50px;
    width: 100%;
    position: sticky;
    top: 0;
    background: var(--main-bg-color-1);
    z-index: 100;
}

.type-head-title {
    height: 100%;
    width: 100%;
    font-size: var(--font-size-small-1);
    font-weight: bold;
}

.xdata-type-content {
    width: calc(100% - 20px);
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 30px;
}