.system-env {
    height: 55px;
    width: 100%;
    display: flex;
}

.system-env-left {
    height: calc(100% - 10px);
    width: calc(50% - 10px);
    padding-left: 10px;
    padding-top: 10px;
    display: flex;
    align-items: center;
}

.env-hello {
    font-size: var(--font-size-big);
    font-weight: bold;
}

.system-env-right {
    height: calc(100% - 10px);
    width: calc(50% - 10px);
    padding-right: 10px;
    padding-top: 10px;
    display: flex;
    justify-content: right;
    align-items: center;
}

.env-date-info {
    height: 100%;
    display: flex;
    flex-direction: column;
    font-size: var(--font-size-small);
}

.date-info-yearmonth {
    height: 25px;
    line-height: 25px;
    width: 100%;
    color: var(--main-text-color-2);
    text-align: right;
}

.date-info-dateday {
    height: calc(100% - 25px);
    width: 100%;
    display: flex;
    flex-direction: row;
}

.date-info-date {
    width: calc(100% - 16px);
    font-weight: bold;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: right;
    margin-right: 10px;
}

.date-info-day {
    width: 16px;
    font-weight: bold;
    color: var(--primary-color-2);
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 12px;
    gap: 3px;
}

.date-info-day div {
    flex-shrink: 0;
    width: 100%;
    height: 50%;
    text-align: center;
}