.habit-data-app {
    width: 100%;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
}

.habit-data-app-head {
    width: calc(100% - 30px);
    padding: 15px;
}

.habit-head-left {
    width: calc(100% - 50px);
    height: 100%;
    display: flex;
}

.habit-head-title {
    margin-right: 10px;
    font-weight: bold;
    font-size: var(--font-size-small);
    color: var(--primary-color-1);
}

.habit-head-stat {
    color: var(--main-text-color-2);
}

.habit-head-right {
    width: 50%;
    height: 100%;
}

.habit-data-app-main {
    width: calc(100% - 60px);
    padding: 0 30px 15px 30px;
    display: flex;
    gap: 20px;
    overflow-x: auto;
}

.habit-item {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.habit-item-icon {
    font-size: var(--font-size-big);
}

.habit-item-time {
    font-size: var(--font-size-mini);
    color: var(--main-text-color-2);
}

.habit-item-title {
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.habit-item-stat {
}


.done-list {
    width: 100%;
    height: calc(100% - 30px);
    padding-bottom: 30px;
}

.done-list-head {
    line-height: 50px;
    height: 50px;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: var(--font-size-small);
}

.done-list-content {
    width: calc(100% - 20px);
    height: calc(100% - 50px - 20px);
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow: auto;
}

.done-list-item {
    height: 60px;
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: row;
    background: var(--main-bg-color-1);
}

.done-list-item-title {
    height: 100%;
    width: calc(100% - 60px);
    color: var(--main-text-color-2);
    font-size: var(--font-size-mid);
    display: flex;
    align-items: center;
}

.done-list-item-operate {
    width: 60px;
    height: 100%;
}

.done-list-btn {
    font-size: var(--font-size-small);
    font-weight: bold;
    color: var(--primary-color-3);
    cursor: pointer;
}